import React, {} from 'react';


import { differenceInCalendarDays, format, formatISO } from 'date-fns';

/// What should be the tiles for Halloween? Kick off of the festivities?


let displayHP = format(new Date( 2022, 8, 30), "MM/dd/yyyy")

let today = formatISO(new Date())

let hocusPocus = formatISO(new Date( 2022, 8, 30))
console.log (today, hocusPocus)

const daysUntilHP = differenceInCalendarDays(
new Date(hocusPocus),
new Date(today)
)



// var day1 = new Date(now);
// var day2 = new Date("10/31/2022");

// var difference= Math.abs(day2-day1);
