function Upcoming() {


    return (
    <>

  <div className="col-md">
    <div className="border m-3">
      <h2 className='text-center'>Upcoming</h2>
      <ul>
        <li className='fw-bold'>Hocus Pocus 2: Disney+ Premier {displayHP} in {daysUntilHP} days.</li>
      </ul>
      <br></br>
        <br></br>
        <ul>
        <li>Freeform 31 nights of October (TBD).</li>
        <li>AMC Fearfest 2022 (10/1/2022).</li>
        <li>Disney Channel Monstober (9/25/2022).</li>
        <li>Syfy 31 Days of Halloween (TBD).</li>
        <li>TMC October Horror (10/1/2022).</li>
        <li>Ghostober 2022- Travel/Discovery (TBD).</li>
      </ul>
    </div>
    </div>

    </>
    );
  };
    
export default Upcoming;

