import React, {} from 'react';
// import sleepyhollow from '../assets/sleepy-hollow.jpg';
import Upcoming from '../components/halloween/upcoming';
import Movies from '../components/halloween/movies';

import { differenceInCalendarDays, formatISO } from 'date-fns';

/// What should be the tiles for Halloween? Kick off of the festivities?npm 

let today = formatISO(new Date())

let halloween = formatISO(new Date( 2022, 9, 31))


const result = differenceInCalendarDays(
new Date(halloween),
new Date(today)
)

console.log(result)

